import { Component, Prop, Event, EventEmitter, h, Host, Listen, Element } from '@stencil/core';

export type CheckboxGroupType = 'checkbox' | 'radio' | 'icon-simple';

@Component({
  tag: 'bh-checkbox-group',
  styleUrl: 'bh-checkbox-group.css',
  shadow: true,
})
export class BhCheckboxGroup {
  @Element() el!: HTMLElement;

  /**
   * The type of input controls in the group
   */
  @Prop() type: CheckboxGroupType = 'checkbox';

  /**
   * The name for radio group (required for radio type)
   */
  @Prop() name: string = '';

  /**
   * Current selected value(s)
   * For checkbox: array of values
   * For radio: single value string
   */
  @Prop({ mutable: true }) value: string | string[] = [];

  /**
   * Whether all items in the group are disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Emitted when selection changes
   */
  @Event() bhChange!: EventEmitter<string | string[]>;

  @Listen('bhItemChange')
  handleItemChange(event: CustomEvent<{ value: string; checked: boolean }>) {
    event.stopPropagation();
    const { value: itemValue, checked } = event.detail;

    if (this.type === 'radio' || this.type === 'icon-simple') {
      // Radio behavior: only one can be selected
      if (checked) {
        this.value = itemValue;
        this.updateChildItems();
        this.bhChange.emit(this.value);
      }
    } else {
      // Checkbox behavior: multiple can be selected
      const currentValues = Array.isArray(this.value) ? [...this.value] : [];

      if (checked) {
        if (!currentValues.includes(itemValue)) {
          currentValues.push(itemValue);
        }
      } else {
        const index = currentValues.indexOf(itemValue);
        if (index > -1) {
          currentValues.splice(index, 1);
        }
      }

      this.value = currentValues;
      this.bhChange.emit(this.value);
    }
  }

  componentDidLoad() {
    this.updateChildItems();
  }

  private updateChildItems() {
    const items = this.el.querySelectorAll('bh-checkbox-group-item');

    items.forEach((item: HTMLBhCheckboxGroupItemElement) => {
      // Set type from group
      item.type = this.type;

      // Set name for radio groups
      if (this.type === 'radio' || this.type === 'icon-simple') {
        item.name = this.name;
      }

      // Set disabled state
      if (this.disabled) {
        item.disabled = true;
      }

      // Update selection state
      if (this.type === 'radio' || this.type === 'icon-simple') {
        item.selected = item.value === this.value;
        item.checked = false;
      } else {
        const values = Array.isArray(this.value) ? this.value : [];
        item.checked = values.includes(item.value);
        item.selected = false;
      }
    });
  }

  render() {
    const role = this.type === 'radio' || this.type === 'icon-simple' ? 'radiogroup' : 'group';

    return (
      <Host>
        <div
          class="checkbox-group"
          role={role}
          aria-disabled={this.disabled ? 'true' : null}
        >
          <slot />
        </div>
      </Host>
    );
  }
}
